  height: 200px;
  padding: 0;
}
.news .flex .nan {
  text-align: left;
  display: flex;
  margin-top: 60px;
  flex-direction: column;
  margin-left: 20px;
}
.news .flex .nan h4 {
  font-size: 28px;
  font-weight: 400;
  line-height: 38px;
  color: #191919;
}
footer {
  width: 100%;
  margin-top: 70px;
  background: #000000;
}
footer p.text {
  padding-top: 180px;
  text-align: left;
}
footer .list {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
}
footer .list span {
  background: #FF4D01;
  padding: 2px 8px;
  color: #FF4D01;
}
footer ul p.text {
  text-align: left;
  padding-bottom: 30px;
}
@media (max-width: 767px) {
  footer ul p.text {
    text-align: center;
  }
}
footer ul li {
  margin: 15px 0px;
  text-align: left;
}
@media (max-width: 767px) {
  footer ul li {
    text-align: center;
  }
}
footer ul li a {
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
  color: white;
}
footer .block p.text {
  padding-bottom: 40px;
}
@media (max-width: 991px) {
  footer .block p.text {
    text-align: center;
  }
}
footer .block .car {
  margin: 20px 0px;
  width: 250px;
  height: 100px;
  border: 2px solid #FF4D01;
}
@media (max-width: 991px) {
  footer .block .car {
    margin: auto;
  }
}
footer .block .car .mmm {
  padding: 30px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}
footer .block .car .mmm svg {
  width: 30px;
  height: 30px;
  padding: 3px 8px;
  background-color: #FF4D01;
}
footer .block .car .mmm .yon {
  margin: 0px 15px;
  padding: 0;
}
footer .block .car .mmm .yon h5 {
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: white;
  text-align: left;
}
footer .block .car .mmm .yon h6 {
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: white;
  text-align: left;
  margin-top: 5px;
}
@media (max-width: 991px) {
  footer p.text {
    text-align: center;
  }
}
footer .nan {
  display: flex;
  margin-top: 180px;
}
@media (max-width: 991px) {
  footer .nan {
    justify-content: center;
  }
}
footer .nan span {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  color: #FF4D01;
  text-align: left;
}
footer .nan h4 {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  color: white;
  text-align: left;
  margin-right: 10px;
}
body {
  font-family: "Lato", "san-serif";
}
a.BgRed {
  background: #FF4D01;
  padding: 22px 35px;
}
a.BgRed:hover {
  cursor: pointer;
  transition: 1s;
  background-color: white;
  color: black;
}
@media (max-width: 991px) {
  a.BgRed {
    display: flex;
    justify-content: center;
    width: 100%;
  }
}
p.text {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  color: white;
  text-align: center;
  padding-top: 20px;
}
@media (max-width: 767px) {
  p.text {
    font-size: 15px;
    line-height: 25px;
  }
}
h2.size {
  font-size: 54px;
  font-weight: 400;
  line-height: 62px;
  color: #191919;
  margin-top: 2px;
}
h2.size .sm-block {
  font-size: 30px;
  line-height: 36px;
}
.activeBars {
  display: none;
}
@media (max-width: 991px) {
  .activeBars {
    display: block;
  }
}
.activeMenuBars {
  display: block;
}
@media (max-width: 991px) {
  .activeMenuBars {
    display: none;
  }
}
